<template>
  <div>
    <el-form ref="loginForm" :model="form" :rules="rules" label-width="80px" class="login-box">
      <h3 class="login-title"></h3>

      <el-form-item label="" prop="email">
        <el-input type="email" placeholder="" v-model="form.email"/>
      </el-form-item>

      <el-form-item label="" prop="phone">
        <el-input type="text" placeholder="" v-model="form.phone"/>
      </el-form-item>

      <el-form-item label="" prop="password">
        <el-input type="password" placeholder="" v-model="form.password"/>
      </el-form-item>

      <el-form-item label="" prop="validatorQuestion">
        <el-input type="question" placeholder="" v-model="form.validatorQuestion"/>
      </el-form-item>

      <el-form-item label="" prop="validatorAnswer">
        <el-input type="answer" placeholder="" v-model="form.validatorAnswer"/>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" v-on:click="onSubmit('loginForm')"></el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "SignUp",
  data() {
    return {
      msg: '',
      form: {
        email:'',
        phone: '',
        password: '',
        validatorQuestion:'',
        validatorAnswer:'',
      },

      //  el-form-item  prop 
      rules: {
        phone: [{required: true, message: '', trigger: 'blur'}, {
          len: 11,
          message: '',
          trigger: 'blur'
        }],
        password: [{required: true, message: '', trigger: 'blur'}, {
          min: 6,
          max: 16,
          message: '',
          trigger: 'blur'
        }],
        email: [{required: true, message: '', trigger: 'blur'}, {
          type: 'email',
          message: '',
          trigger: 'blur'
        }],
        validatorQuestion: [{required: true, message: '', trigger: 'blur'}],
        validatorAnswer: [{required: true, message: '', trigger: 'blur'}],
      }

    }
  },
  methods: {
    onSubmit(formName) {
      // 

      console.log(this.$refs[formName])
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios({
            url: "/user/modify/account",
            method:'POST',
            data: JSON.stringify({
              "email": this.form.email,
              "phoneNum": this.form.phone,
              "password": this.form.password,
              "verifyQues": this.form.validatorQuestion,
              "verifyAns": this.form.validatorAnswer
            }),
            dataType: "json",
            headers:{'Content-Type': "application/json;charset=utf-8"}
          }).then(res => {
            console.log(res)
            console.log(res.data)
            this.$alert("! " + "  User ID : " + res.data)
            //
            this.$router.push({path: '/contain/login',query:{uid: res.data, email:this.form.email, phoneNum:this.form.phone, verifyQues:this.form.validatorQuestion, verifyAns:this.form.validatorAnswer}})
          }).catch(err => {

            console.log(err.message)
            this.$alert(" " + " ")
          })
        } else {
          this.$alert("")
          return false;
        }
      });
    }
  }
}
</script>

<style lang="scss" scoped>
.login-box {
  border: 1px solid #DCDFE6;
  width: 350px;
  margin: 180px auto;
  padding: 35px 35px 15px 35px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  box-shadow: 0 0 25px #909399;
}

.login-title {
  text-align: center;
  margin: 0 auto 40px auto;
  color: #303133;
}
</style>




